:host .loading-bar {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: var(--mdx-comp-loadingbar-sizing-height-default);
  overflow-x: hidden;

  &__line {
    position: absolute;
    opacity: 1;
    background: var(--mdx-comp-loadingbar-background-color-default);
    width: 100%;
    height: var(--mdx-comp-loadingbar-sizing-height-default);
  }

  &__subline {
    position: absolute;
    background-color: var(--mdx-comp-loadingbar-color-default);
    height: var(--mdx-comp-loadingbar-sizing-height-default);
  }

  &__subline--increase {
    animation: increase 2s infinite;
  }

  &__subline--decrease {
    animation: decrease 2s 0.5s infinite;
  }
}

@keyframes increase {
  from {
    left: -5%;
    width: 5%;
  }
  to {
    left: 130%;
    width: 100%;
  }
}
@keyframes decrease {
  from {
    left: -80%;
    width: 80%;
  }
  to {
    left: 110%;
    width: 10%;
  }
}
